.greeting {
    display: flex;
    align-items: flex-start;
}

.error {

    color: red;
    height: 21px;
    margin-left: 10px;

}

.input {
    margin: 5px 10px;
    height: 30px;
}

.input:focus {
    outline: none;
    border: #282c34 solid 2px;
}

.errorInput {
    margin: 5px 10px;
    border: 2px solid red;
    outline: none;
    height: 30px;
}

.button {
    margin: 5px;
    padding: 7px 45px;
    font-size: 16px;
    background: #2196F3;
    color: white;
    outline: none;

}

.button:focus {
    /*outline: #7f7f7f solid 1px;*/
}

.button:active{
    background: #2196F3;
}
.button:disabled{
    color: rgba(255,255,255,.3);
}

.count{
    margin: 10px;
}

