.default {

}

.button{
    margin: 5px;
    background-color: #2196F3;
    color: white;
    border: none;
    padding: 14px 28px;
    cursor: pointer;
    font-size: 16px;
}

.default:hover {
    background: #0b7dda;
}

.default:disabled{
    color: rgba(255,255,255,.3);
}

.red {
    background: red;
    border: none;
    padding: 14px 28px;
    cursor: pointer;
    font-size: 16px;

}